window.addEventListener('DOMContentLoaded', function(){

    eventSlider();
    creaNeon();

});


function eventSlider(){

    let sliderImg = document.querySelectorAll('.slider-img');

    sliderImg.forEach( element =>{
        element.addEventListener('click', function(){
            sliderImg.forEach( element => element.classList.remove('active'));
            element.classList.add('active');
        })
    });

}
function creaNeon(){

    let texto = document.querySelector('#text');
    let fuentes = document.querySelector('#fuentes');
    

    document.querySelector('#boton-form').addEventListener('click', function(e){
        e.preventDefault();
        window.open('https://wa.link/un2vy6');
    });

    texto.addEventListener('input', function(e){
        e.preventDefault();

        let text = e.target.value;
        let font = fuentes.value;
        crearNeon(font, text);
    });

    fuentes.addEventListener('input', function(e){
        e.preventDefault();
        let text = document.querySelector('.neon-crea h3').textContent;
        let font = e.target.value;
        crearNeon(font , text);

    });


} 
function crearNeon(clase, text){
    let neon = document.querySelector('.neon-crea');
    neon.innerHTML = `<h3 class="${clase}">${text}</h3>`
}